
import { Component, OnInit} from '@angular/core';
declare var google:any;
@Component({
  selector: 'chart',
  template: ``
})
export class GaugeChartBaseComponent implements OnInit {
  private static googleLoaded:any;

  constructor(){
      console.log("Here is GoogleChartComponent")
  }

  getGoogle() {
      return google;
  }
  ngOnInit() {
    console.log('ngOnInit');
    if(!GaugeChartBaseComponent.googleLoaded) {
      GaugeChartBaseComponent.googleLoaded = true;
      google.charts.load('current',  {packages: ['corechart', 'gauge']});
    }
    google.charts.setOnLoadCallback(() => this.drawGraph());
  }

  drawGraph(){
      console.log("DrawGraph base class!!!! ");
  }

  createGaugeChart(element:any):any {
    google.charts.load('current',  {packages: ['corechart', 'gauge']});
    /*setTimeout(function() {
      return new google.visualization.GaugeChart(element);
    }, 2000);
*/
   return new google.visualization.Gauge(element);

  }

  createDataTable(array:any[]):any {

    return google.visualization.arrayToDataTable(array);
  }
}


