import { Component, OnInit } from '@angular/core';
import {MotorService} from '../../Services/MotorService';

@Component({
  selector: 'app-motor-summary',
  templateUrl: './motor-summary.component.html',
  styleUrls: ['./motor-summary.component.css']
})
export class MotorSummaryComponent implements OnInit {
  public Motor:any;
  public JSON;
  constructor(private motorService:MotorService) {
    this.JSON=JSON;
   }

  ngOnInit() {
    this.motorService.MotorDataSubject.subscribe(data=>{
      this.Motor=data[0];
      console.log(JSON.parse(this.Motor.MotorObject).Name);
    });
  }

}



