import { Component,Input } from '@angular/core';
import { NotificationService} from '../../Services/NotificationService'; 

import {config} from  '../../Configuration/MasterConfiguration';


import { OnInit} from '@angular/core';
@Component({
  selector: 'notifications',
  templateUrl: './notifications.component.html' 
})
 

export class NotificationsComponent implements OnInit  {

      constructor (private ss :NotificationService)
      {       
        this.JSON=JSON;
      }
      
          
      @Input() currentPlantId : number;    
      notificationlist : any;
      filterNotlist :any;
      JSON :any; 
           
      ngOnInit(): void {      
        
         if(this.currentPlantId== null)
         { 
              this.currentPlantId=2;   
         }      
         this.GetStationListForPlant();           
      }

      
       
     GetStationListForPlant() :void
      {                
      this.ss.GetNotificationList(config.PlantName,'-1','-1').subscribe(data => this.notificationlist = data); 
      
  
      }

      
      myFunction(rowkey, event: MouseEvent) {
        console.log(rowkey);
        
        this.filterNotlist =this.notificationlist.filter(x=> x.RowKey==rowkey);
        console.log("motorlist" +this.filterNotlist);
          
         if(this.filterNotlist.length>0)
           {
             var popup = document.getElementById("notPop");
             popup.classList.toggle("show");
   
             var x = event.clientX;
             var y = event.clientY;
            console.log(x + "  "+ y)
            
           }
   
      
   }


 
	}


