import { Component, OnInit, Input } from '@angular/core';
import {  StationService} from '../../Services/StationService';
import {  MotorService} from '../../Services/MotorService';

import {config} from  '../../Configuration/MasterConfiguration';
import { CustomPipeComponent } from '../custom-pipe/custom-pipe.component';


@Component({
  selector: 'stations',
  templateUrl: './stationlist.component.html' 
  
})
export class StationlistComponent implements OnInit {

  constructor (private ss :StationService, private  ms:MotorService)
  {       
     this.JSON=JSON;
  }
  
  @Input() currentPlantId : number;    
  stationlist : any;
  motorlist: any;
  filtermotorlist :any;
  JSON :any; 
  mystation :string ='Station A'
    
       
  ngOnInit(): void {      
    
     if(this.currentPlantId== null)
     { 
          this.currentPlantId=2;   
     }      
     this.GetStationListForPlant();
     this.GetMotorList();
  }
   
 GetStationListForPlant() :void
  {                
  this.ss.GetStationListforPlant(config.PlantName).subscribe(data => this.stationlist = data); 
  }

  GetMotorList() :void
  {        
  this.ms.GetMotorList(config.PlantName,'').subscribe(data => {this.motorlist = data;console.log(this.motorlist );}); 
  }

   myFunction(stationname, event: MouseEvent) {
     console.log(stationname);
     
     this.filtermotorlist =this.motorlist.filter(x=> x.StationName==stationname);
     console.log("motorlist" +this.filtermotorlist);
       
      if(this.filtermotorlist.length>0)
        {
          var popup = document.getElementById("myPopup");
          popup.classList.toggle("show");

          var x = event.clientX;
          var y = event.clientY;
         console.log(x + "  "+ y)
         
        }

   
}
}



