
import { Component, OnInit ,Input } from '@angular/core';
import { DonutBaseChartComponent} from '../donut-base-chart/donut-base-chart.component';
import {  Station} from '../../Entities/Station';

import {  MotorService} from '../../Services/MotorService';

import {config} from  '../../Configuration/MasterConfiguration';
 
@Component({
  selector: 'motor-status-chart',
  templateUrl: './motor-status-chart.component.html' 
     
})
export class MotorStatusChartComponent  extends DonutBaseChartComponent {

   stationlist : Station[] ;
   station:Station;
  private options;
  private data;
  private chart;  

  v:any;
  result :any;
  constructor(private ss :MotorService ){ 
    
    super();        
    this.getMotorStatus();
   
  }

  getMotorStatus(){
	  
	  // hard coded 
	        this.result = [
		['StationHealth', 'NumOfStation'],
		['Green',     5],
		['Amber',     1],
		['Red',  2]
	  ]  
		 
	    
    this.ss.getOverAllMotorStatus(config.PlantName,'').subscribe(result =>{ 
        this.v=result;
        setTimeout( this.drawGraph(),2000);
       });
      
     
  }

 
   
  drawGraph(){

    let stList = this.v.json()['value'];     
    if(config.PlantName!='')
     {       
      stList=stList.filter(x=>x.PlantName==config.PlantName);
     }
    
	  let Green =stList.filter(x=>x.Status=='Green').length;
	  let Red =stList.filter(x=>x.Status=='Red').length;
	  let Amber =stList.filter(x=>x.Status=='Amber').length;
	  
	    this.result = [
		['StationHealth', 'NumOfStation'],
		['Green',     Green],
		['Amber',     Amber],
		['Red',  Red]
	  ]  	 

	   
    console.log("DrawGraph Evolution...");  

      
   
    this.data = this.createDataTable(this.result );

    this.options = {
      /*title: 'Evolution, 2014', */
      chartArea:{left:0,width:"90%",height:"500"} ,
      hAxis: {
        title: 'Value in USD',
        minValue: 0
      } ,
      'legend':'none',
      vAxis: {
        title: 'Members'
      },
      "colors":["#008000","#FFC200", "#FF0000"],
      'width':280,
      'height':200, 
        pieHole: 0.4   
     

    };

    this.chart = this.createBarChart(document.getElementById('donutChart'));
    this.chart.draw(this.data, this.options);
  }
}


