import { Component, OnInit, Renderer } from '@angular/core';
import {MotorService} from '../../Services/MotorService';
import {Motor} from '../../Entities/Motor';
import {StationService} from '../../Services/StationService';
import {config} from  '../../Configuration/MasterConfiguration';

import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-motor-list',
  templateUrl: './motor-list.component.html',
  styleUrls: ['./motor-list.component.css'],
  providers:[MotorService]
})
export class MotorListComponent implements OnInit {
  public motorList;
  public JSON;
  public locationImage;
  public stationList;
  public motorFilteredList;
  public default=true;
  public flag=true;
  public currentStation;
  
    
  constructor(private motorService:MotorService,private stationService:StationService,
    private renderer :Renderer,   private route: ActivatedRoute ) {
    this.JSON=JSON;
    this.locationImage="/assets/images/location.png";    
   }

  ngOnInit() {
    this.currentStation = this.route.snapshot.paramMap.get('StationName');
    console.log("current Station "+this.currentStation);
     
    if(this.currentStation!=null && this.currentStation!='')
      this.flag=false;
    this.getMotorList();
    this.GetStationList();
   
    
  }
  onStationChange(event:Event):void {
    this.flag=false;
    this.default=true;
   console.log((event.target as HTMLSelectElement).value);
   this.applyStationFilter((event.target as HTMLSelectElement).value);
  
}
  getMotorList(){ 
    this.motorService.GetMotorList(config.PlantName,'').subscribe(res=>{
      this.motorList=res;
      if(this.currentStation)
      this.motorFilteredList=this.motorList.filter(motor=>motor.StationName==this.currentStation);
      console.log("Received by observer"+JSON.parse(this.motorList[0].MotorObject).Name);
    });
  }
  GetStationList(){
    this.stationService.GetStationListforPlant(config.PlantName).subscribe(
      res=>{
      
        this.stationList=res;
      }
    
    );}
    applyStationFilter(StationName){

      this.currentStation =StationName;
      this.motorFilteredList=this.motorList.filter(motor=>motor.StationName==StationName);

      
    }
    applyMotorFilter(MotorName){
      return this.motorList.filter(motor=>JSON.parse(motor.MotorObject).Name==MotorName);
    }    
    public displayMotorData(motorname,divid,event:Event):void{
      this.default=false;
      this.flag=true;
      //this.motorService.setMotor(this.applyMotorFilter((event.target as HTMLButtonElement).name));
      this.motorService.setMotor(this.applyMotorFilter(motorname));

     
     /*let  myid = document.getElementById('myid');
      this.renderer.setElementClass(myid,"SelectedNav",false); */
       console.log('hi');
       if(this.motorFilteredList) 
         {
            for( let motor in this.motorFilteredList  )
              {
                let motorid= this.motorFilteredList[motor]["RowKey"];   
                console.log(motorid);       
                let  mid = document.getElementById(motorid);
                this.renderer.setElementClass(mid,"SelectedNav",false);
              }
         }

         let  selecteddiv = document.getElementById(divid);
         this.renderer.setElementClass(selecteddiv,"SelectedNav",true);


    }

    public displayDashboardData(Stationname,event:Event):void{
      this.flag=false;
      this.default=true;      
    /*  let  myid = document.getElementById('myid');
     this.renderer.setElementClass(myid,"SelectedNav",true);
     */
      console.log('hi');
      if(this.motorFilteredList)

        {
      for( let motor in this.motorFilteredList  )
        {
          let motorid= this.motorFilteredList[motor]["RowKey"];   
          console.log(motorid);       
          let  mid = document.getElementById(motorid);
          this.renderer.setElementClass(mid,"SelectedNav",false);
        }
      }
      //this.motorService.setMotor(this.applyMotorFilter((event.target as HTMLButtonElement).name));
     // this.motorService.setMotor(this.applyMotorFilter(Stationname));
    }
}



