import { Component, OnInit} from '@angular/core';
declare var google:any;
@Component({
  selector: 'donut-base-chart',
  template: ``
})
export class DonutBaseChartComponent implements OnInit {
  private static googleLoaded:any;

  constructor(  ){
      console.log("Here is GoogleChartComponent")
  }

  getGoogle() {
      return google;
  }
  ngOnInit() {
    
    if(!DonutBaseChartComponent.googleLoaded) {
      DonutBaseChartComponent.googleLoaded = true;
      google.charts.load('current',  {packages: ['corechart', 'bar']});
    }
    google.charts.setOnLoadCallback(() => this.drawGraph());
 
  }

  drawGraph(){
      console.log("DrawGraph base class!!!! ");
  }

  createBarChart(element:any):any {
      return new google.visualization.PieChart(element);
  }

  createDataTable(array:any[]):any {
    if(google.visualization!=null)
        return google.visualization.arrayToDataTable(array);
        else 
          return null;
  }
}


