import { Component,Input } from '@angular/core';
import { NotificationService} from '../../Services/NotificationService'; 
import {config} from  '../../Configuration/MasterConfiguration';
import {MotorService} from '../../Services/MotorService'

@Component({
  selector: 'app-motor-alert',
  templateUrl: './motor-alert.component.html'
})
export class MotorAlertComponent {

  public Motor:any;
  public JSON;
  public notificationlist :any []=[];
        constructor (private ss :NotificationService, private motorService:MotorService)
        {       
         this.JSON=JSON;
        }       
            
        @Input() currentPlantId : number;    
       
             
        ngOnInit(): void {      
          this.motorService.MotorDataSubject.subscribe(data=>this.Motor=data[0]);
          this.motorService.MotorNotification.subscribe(message=>console.log('Motor Notification'+message));
          this.motorService.MotorNotification.subscribe(data=>this.notificationlist.push(data));                
        }
     
    }



