import  {  Injectable }  from '@angular/core';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import{Subject} from 'rxjs/Subject';
import{Observable } from 'rxjs/Rx';
import {config} from  '../Configuration/MasterConfiguration';
  

//the injectable attribute to generate the metadata to make the service available through 
//Dependency Injustion(DI) in other modules.

@Injectable()
export class MotorService
{
    
    private static serviceInstance :MotorService =null; 
    private testv :string;
    cachedData:any;
    constructor(private http: Http) { 
                
     }
    public MotorDataSubject=new Subject<any>();
    public MotorNotification=new Subject<any>();
     // Return the instance of the service
     public static getInstance(http: Http): MotorService {
        if (MotorService.serviceInstance === null) {
            MotorService.serviceInstance = new MotorService(http);
        }
        return MotorService.serviceInstance;
      } 
     
     public setMotor(data){
	  this.MotorDataSubject.next(data);
  }

  public setNotification(data){
      this.MotorNotification.next(data);
  }
  
    
    public GetMotorList(plantname:string,stationName:string) :Observable<any>  { 
         
        if(this.cachedData){
            console.log('motor service:'+this.cachedData);
            return Observable.of(this.cachedData);
          }

        return this.http.get(config.MotorUrl)    
        .map(res => this.GetJsonData(res,plantname,stationName) );
	}	

    public getOverAllMotorStatus(plantname:string,stationName:string):Observable<any>{ 	 
        return this.http.get(config.MotorUrl);
       //.map(res=>{ this.CalculateMotorStatus(res,plantname,stationName);});
        }
    
    public GetNotificationList():Observable<any>{
    return this.http.get(config.MotorUrl)
            .map(res=>{
                console.log(res.json()['value']);
                return res.json()['value']; ;
            });
    }
  
    private  GetJsonData(xmldata,plantname,stationName)
    {      
        
         let jsonobject = xmldata.json()['value']; 
         
         if(plantname!='')
            {
                
            jsonobject=jsonobject.filter(x=>x.PlantName==plantname);
            }
            console.log(jsonobject);

            this.cachedData=jsonobject;

            console.log("getjsondata: "+ this.cachedData);
         return jsonobject;
     
  }

  

private handleError(error: any)  {
    console.error('An error occurred', error); // for demo purposes only
     return null;
  }

  private CalculateMotorStatus(res,plantname,stationName)
  {
      
      let stList=this.GetJsonData(res,plantname,stationName);
      console.log(stList);
      let Green =stList.filter(x=>x.Status=='Green').length;
      let Red =stList.filter(x=>x.Status=='Red').length;
      let Amber =stList.filter(x=>x.Status=='Amber').length;     
 
      let result = [
        ['StationHealth', 'NumOfStation'],
        ['Green',     Green],
        ['Amber',     Amber],
        ['Red',  Red]
      ]      
      
      return  result;
       
  }

}






