import {RouterModule,Routes} from '@angular/router';
import { NgModule } from '@angular/core';


import { GaugeChartComponent } from './charts/gauge-chart/gauge-chart.component';
import { MotorListComponent } from './Components/motor-list/motor-list.component';
import { MotorManagementComponent } from './motor-management/motor-management.component';
import { hclplantdashboard } from './Components/Plant/hclplantdashboard';
import { StationComponent } from './Components/Station/StationComponent';
import { GaugeChartBaseComponent } from './charts/gauge-chart-base/gauge-chart-base.component';
import { LoginComponent } from './Components/login/login.component';



const appRoutes:Routes=[
    {  path:'graph',  component:GaugeChartComponent  },
    {  path:'motor/:StationName',  component:MotorListComponent   },
    {  path:'motor',  component:MotorListComponent   },
    {  path:'MotorManagement',  component:MotorManagementComponent  },
    {  path: 'plant',  component: hclplantdashboard,  data :{ }     },  
    { path: '',  redirectTo: '/login',   pathMatch: 'full'  },
    { path: 'station/:id',      component: StationComponent },
    { path: 'login', component: LoginComponent }
  ];



      @NgModule({
         
        imports: [
          RouterModule.forRoot(appRoutes)
        ],
        exports: [RouterModule]
      })
      export class AppRoutingModule {
      
      }


